
'use client';

import Image from 'next/image';
import Link from 'next/link';
import type { Building, User } from '@/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { ThumbsUp, User as UserIcon, Scaling, ShieldAlert } from 'lucide-react';
import VoteButton from './VoteButton';
import { cn } from '@/lib/utils';

interface BuildingCardProps {
  building: Building;
  currentUser: User | null;
}

export default function BuildingCard({ building, currentUser }: BuildingCardProps) {
  const isInteractive = building.status === '有效';
  const CardWrapper = isInteractive ? Link : 'div';

  return (
    <Card className={cn(
      "overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 flex flex-col h-full group relative",
      !isInteractive && "cursor-not-allowed"
    )}>
      <CardWrapper
        href={isInteractive ? `/building/${building.id}` : '#'}
        onClick={!isInteractive ? (e) => e.preventDefault() : undefined}
        className="flex flex-col flex-grow"
        aria-disabled={!isInteractive}
        tabIndex={!isInteractive ? -1 : undefined}
      >
        <CardHeader>
          <CardTitle className={cn(
            "text-2xl font-semibold text-primary",
            isInteractive && "group-hover:text-accent transition-colors"
          )}>
            {building.name}
          </CardTitle>
          <div className="flex justify-between items-center">
            <CardDescription className="flex items-center text-muted-foreground">
              <UserIcon className="mr-1.5 h-4 w-4" /> 作者: {building.author}
            </CardDescription>
            <Badge variant="secondary" className="flex items-center">
              <Scaling className="mr-1 h-3 w-3" />
              {building.size}
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="flex-grow">
          <div className="relative aspect-video w-full mb-4 rounded-md overflow-hidden">
            <Image
              src={building.imageUrl.replace('800x600', '600x400')}
              alt={building.name}
              layout="fill"
              objectFit="cover"
              data-ai-hint={building.aiHint || "minecraft building"}
              className={cn(
                "transform transition-transform duration-300",
                isInteractive && "group-hover:scale-105"
              )}
            />
            {!isInteractive && (
              <div className="absolute inset-0 bg-red-700/50 backdrop-blur-sm flex flex-col items-center justify-center text-center p-4 z-10 rounded-md">
                <ShieldAlert className="h-12 w-12 text-white mb-2" />
                <p className="text-lg font-semibold text-white">状态: {building.status}</p>
                <p className="text-sm text-red-100">此作品当前无法访问或投票。</p>
              </div>
            )}
          </div>
          <p className="text-foreground/90 leading-relaxed line-clamp-3">{building.description}</p>
        </CardContent>
      </CardWrapper>
      <CardFooter className="flex justify-between items-center mt-auto pt-4 border-t">
        <div className="flex items-center text-accent">
          <ThumbsUp className="mr-1.5 h-5 w-5" />
          <span className="font-semibold text-lg">{building.votes}</span>
          <span className="ml-1 text-sm">票</span>
        </div>
        <VoteButton
          buildingId={building.id}
          currentUser={currentUser}
          initialVotes={building.votes}
          buildingStatus={building.status}
        />
      </CardFooter>
    </Card>
  );
}
