
'use client';

import { useState, useTransition } from 'react';
import { Button } from '@/components/ui/button';
import { ThumbsUp, Loader2 } from 'lucide-react';
import { submitVote } from '@/app/actions';
import { useToast } from '@/hooks/use-toast';
import type { User, BuildingStatus } from '@/types';

interface VoteButtonProps {
  buildingId: string;
  currentUser: User | null;
  initialVotes: number; 
  buildingStatus: BuildingStatus; // 新增属性
}

export default function VoteButton({ buildingId, currentUser, initialVotes, buildingStatus }: VoteButtonProps) {
  const [isPending, startTransition] = useTransition();
  const { toast } = useToast();

  const canVote = currentUser && buildingStatus === '有效';

  const handleVote = async () => {
    if (!currentUser) {
      toast({
        title: '请先登录',
        description: '登录后才能为作品投票。',
        variant: 'default',
        duration: 3000,
      });
      return;
    }

    if (buildingStatus !== '有效') {
      toast({
        title: '无法投票',
        description: `此作品当前状态为 "${buildingStatus}"，不能进行投票。`,
        variant: 'destructive',
        duration: 3000,
      });
      return;
    }

    startTransition(async () => {
      const result = await submitVote(buildingId);
      if (result.success) {
        toast({
          title: '投票成功！',
          description: result.message,
          variant: 'default',
        });
      } else {
        toast({
          title: '投票失败',
          description: result.message,
          variant: 'destructive',
        });
      }
    });
  };

  return (
    <Button 
      onClick={handleVote} 
      disabled={isPending || !canVote} 
      variant="outline" 
      className="border-accent text-accent hover:bg-accent hover:text-accent-foreground disabled:opacity-60 disabled:cursor-not-allowed disabled:bg-transparent disabled:hover:text-accent disabled:hover:border-accent"
      aria-label={`为 ${buildingId} 投票`}
      title={!canVote && buildingStatus !== '有效' ? `作品状态为 "${buildingStatus}"，无法投票` : (!currentUser ? '请登录后投票' : '为作品投票')}
    >
      {isPending ? (
        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
      ) : (
        <ThumbsUp className="mr-2 h-4 w-4" />
      )}
      {isPending ? '投票中...' : '投票'}
    </Button>
  );
}
